@import 'variables.scss';

.paginate {
    margin-top: 20px;
}

.pagenum {
    background-color: transparent;
    font-weight: bold;
    padding: 5px 15px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
}

.pagenum.active {
    color: #fff;
    background-color: $quanta-color-purple1;
    border-color: $quanta-color-purple1;
}

button:focus {
    outline: none;
}